.shop-title-wrap {
  position: relative;
  text-align: center;
  margin-top: map_get($global-spacing, c);
  border-top: .13333rem solid #1a1a1a;
  border-bottom: .13333rem solid #1a1a1a;
  padding: map_get($global-spacing, a) 0;
  font-size: map_get($global-font-size, a);
  color: map_get($global-color, a);

  h2 {
    position: relative;
    line-height: 1.178;

    &:first-child:after {
      display: block;
      content: '';
      margin: 10px auto 15px auto;
      height: 3px;
      width: 30px;
      background-color: map_get($global-color, a);
    }
  }

  p {
    line-height: 1;
    margin-top: map_get($global-spacing, b);
  }
}